
a {
  /* color: #9164bc; */
  color: #766C7F;
}
a:hover, a:focus {
  /* color: #766C7F; */
  color: #9164bc;
}
.russian-green {
  color: #6d9f71;
}
.red-text {
  color: #d00d2d;
}
.mummys-tomb {
  color: #808D8E;
}
.deep-lilac {
  color: #9b64bc;
}
.lue-bell {
  color: #A3A5C3;
}
.old-lavender {
  color: #766C7F;
}
.pastel-blue {
  color: #A9D2D5;
}
::-moz-selection {
  color: white;
  background: #766C7F; /* WebKit/Blink Browsers */
}
::selection {
  color: white;
  background: #766C7F; /* WebKit/Blink Browsers */
}
::-moz-selection {
  color: white;
  background: #766C7F; /* Gecko Browsers */
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.25s ease-out;
  transition: opacity 0.25s ease-out;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.pb-2{
  padding-bottom: 2rem;
}
.pb-3 {
  padding-bottom: 3rem;
}
.pb-4 {
  padding-bottom: 4rem;
}
.pt-1{
  padding-top: 1rem;
}
.pt-1-half {
  padding-top: 1.5rem;
}
.pt-2 {
  padding-top: 2rem;
}
/* width */
::-webkit-scrollbar {
  width: .5rem;
  max-height: .5rem;
}
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #766C7F; /* WebKit/Blink Browsers */
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #888;
}
.columns {
  margin: 0;
}
a svg {
  width: 10%;
}
@media screen and (min-width: 768px) {
a svg {
    width: 3%;
}
.columns {
    margin-left: -0.75rem;
    margin-right: -0.75rem;
    margin-top: -0.75rem;
}
}
